import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import Footer from '../components/Footer'
import printImg from '../assets/img/print_btn.webp'
import MainAPI from '../assets/APIs/mainApi'
import { useState } from 'react'
import TabTittle from '../components/TabTittle'
import SideBar from '../components/SideBar'
import MainHeader from '../components/MainHeader'
import CommentImg from "../assets/img/comment.gif"
import axios from 'axios'
import SkliStaff from '../components/skeletonComp/Skli_Staff'

function Straps() {
    TabTittle('নোটিশ | জেলা পরিষদ, রাজশাহী।')
    const imgURL = `https://zpr.s3cbd.com/assets/zprstaff/`
    const API = `https://zpr.s3cbd.com/data/staff`
    const [mainData, setmainData] = useState([])
    const [isLoading, setIsLoading] = useState(true)
    console.log(mainData);
    const getAPIData = async (api) => {
        try {
            const res = await axios.get(api)
            const Data = await res.data
            setmainData(Data)
            setIsLoading(false)
        } catch (error) {
            console.log(error.message);
        }
    }
    useEffect(() => {
        getAPIData(API)
    }, []);
    return (
        <>
            <div className="contaier">
                <MainHeader />
                <div className="mainContentPart">
                    <div className='colam'>
                        <div className="pageTittle">
                            <h2><Link to="/" >Hme</Link>/Workers</h2>
                        </div>
                        <div className="newcContent">
                            <div className="tittle text-cente"><h1>জেলা পরিষদের অর্গানোগ্রাম ভূক্ত কর্মকর্তা ও কর্মচারীর তথ্য 111</h1> <button title='print'><img src={printImg} alt="" /></button></div>
                            <table>
                                <tr className='header'>
                                    <th>ক্রমিক</th>
                                    <th>নাম ও পদবী</th>
                                    <th>মোবাইল</th>
                                    <th>ছবি</th>
                                </tr>
                                {
                                    isLoading
                                        ?
                                        <SkliStaff/>
                                        :
                                        <>
                                            {
                                                mainData.map((e, i) => {
                                                    const { name, id, designation, mobile, image } = e
                                                    return (
                                                        <>
                                                            <tr>
                                                                <td rowSpan={'2'} className='text-center'>{i}</td>
                                                                <td>{name}</td>
                                                                <td rowSpan={'2'} className='text-center'><a href={`tel:${mobile}`}>{mobile}</a></td>
                                                                <td rowSpan={'2'} className='text-center'> <img src={imgURL + image} alt="" /> </td>
                                                            </tr>
                                                            <tr>
                                                                <td>{designation}</td>
                                                            </tr>

                                                        </>
                                                    )
                                                })
                                            }
                                        </>
                                }

                            </table>
                        </div>
                    </div>
                    <div className='colam'>
                        <SideBar />
                    </div>
                </div>
                <Footer />
            </div>
            <div className="optionsPart">
                <div className="comment">
                    <img src={CommentImg} alt="" />
                </div>
            </div>
        </>
    )
}

export default Straps
