import React, { useRef } from 'react'
import { Link } from 'react-router-dom'
import Footer from '../components/Footer'
import printImg from '../assets/img/print_btn.webp'
import MainAPI from '../assets/APIs/mainApi'
import { useState } from 'react'
import TabTittle from '../components/TabTittle'
import SideBar from '../components/SideBar'
import MainHeader from '../components/MainHeader'
import CommentImg from "../assets/img/comment.gif"
import LoadingBar from 'react-top-loading-bar'
import { useEffect } from 'react'
import axios from 'axios'
import Skli_Notice from '../components/skeletonComp/Skli_Notice'
const pdfURL = `https://zpr.s3cbd.com/assets/zprnotice/`

function Notice() {
    TabTittle('নোটিশ | জেলা পরিষদ, রাজশাহী।')
    const API = `https://zpr.s3cbd.com/data/notice`
    const [mainData, setmainData] = useState([])
    const [isLoading, setIsLoading] = useState(true)
    const getAPIData = async (api) => {
        try {
            const res = await axios.get(api)
            const Data = await res.data
            setmainData(Data)
            setIsLoading(false)
        } catch (error) {
            console.log(error.message);
        }
    }
    useEffect(() => {
        getAPIData(API)
    }, []);

    return (
        <>
            <div className="contaier">
                <MainHeader />
                <div className="mainContentPart">
                    <div className='colam'>
                        <div className="pageTittle">
                            <h2><Link to="/" >Hme</Link>/Notice</h2>
                        </div>
                        <div className="newcContent">
                            <div className="tittle"><h1>নোটিশ</h1> <button title='print'><img src={printImg} alt="" /></button></div>
                            <div className="scroll-x">
                                <table >
                                    <tr className='header'>
                                        <th>ক্রমিক</th>
                                        <th>শিরোনাম</th>
                                        <th>প্রকাশের তারিখ</th>
                                        <th>ডাউনলোড</th>
                                    </tr>
                                    {
                                        isLoading
                                            ?
                                            <Skli_Notice/>
                                            :
                                            <>
                                                {
                                                    mainData.map((e, i) => {
                                                        const { title, notice_date, id, file } = e
                                                        return (
                                                            <>
                                                                <tr>
                                                                    <td className='text-center'>{i}</td>
                                                                    <td><Link to={`/notice/details/${id}`}>{title}</Link></td>
                                                                    <td className='date text-center'>{notice_date}</td>
                                                                    <td className='text-center download'><a download={true} href={`${pdfURL}${file}`} >ডাউনলোড</a></td>
                                                                </tr>

                                                            </>
                                                        )

                                                    })
                                                }
                                            </>


                                    }
                                    {/* <h1>{mainData[0].id}</h1> */}
                                </table>
                            </div>

                        </div>
                    </div>
                    <div className='colam'>
                        <SideBar />
                    </div>
                </div>
                <Footer />
            </div>
            <div className="optionsPart">
                <div className="comment">
                    <img src={CommentImg} alt="" />
                </div>
            </div>
        </>
    )
}

export default Notice
