import React, { useEffect, useState } from 'react'
import Footer from '../components/Footer'
// Images
import infoImg from "../assets/img/apa_cab.png"
import ch_img from '../assets/img/chairman-sir1.webp'
import pnk_img from '../assets/img/reza_hasan.webp'
import MainHeader from '../components/MainHeader'
import { useParams } from 'react-router-dom'
import MainAPI from '../assets/APIs/mainApi'
import SideBar from '../components/SideBar'
import CommentImg from "../assets/img/comment.gif"
import axios from 'axios'
import { useStrafContext } from '../context/AppContext'
import Skli_Discribe from '../components/skeletonComp/Skli_Discribe'

const imgURL = `https://zpr.s3cbd.com/assets/zprstaff/`;
function Discribe() {
    const [mainData, setMainData] = useState([]);
    const API = `https://zpr.s3cbd.com/data/staff/`;
    const { id } = useParams()
    const [isLoading, setIsLoading] = useState(true)
    
    const singleStrafData = async (api) => {
        const res = await axios.get(api)
        const Data = await res.data;
        setMainData(Data)
        setIsLoading(false)
    }

    useEffect(() => {
        singleStrafData(`${API}?id=${id}`)
    }, [id])

    return (
        <>
            <div className="contaier">
                <MainHeader />
                <div className="mainContentPart">
                    {
                        isLoading
                        ?
                        <Skli_Discribe/>
                        :
                        <div className='colam describe'>
                        <h1>{mainData.name}</h1>
                        <div className="content">
                            <div className="memberDit">
                                <div className="imgpart rounded">
                                    <img src={imgURL + mainData.image} alt="" />
                                </div>
                                <div className="bayo">
                                    <p>{mainData.staff_details}</p>
                                </div>
                            </div>
                            <div className="maberTable">
                                <table>
                                    <tr className='header'>
                                        <th>নাম</th>
                                        <th>{mainData.name}</th>
                                    </tr>
                                    <tr>
                                        <td>পদবী</td>
                                        <td>{mainData.designation}</td>
                                    </tr>
                                    <tr>
                                        <td>টেলিফোন নম্বর</td>
                                        <td>{mainData.phone}</td>
                                    </tr>
                                    <tr>
                                        <td>ই-মেইল ঠিকানা</td>
                                        <td>{mainData.email1}</td>
                                    </tr>
                                    <tr>
                                        <td>বর্তমান কর্মস্থলে যোগদানের তারিখ</td>
                                        <td>{mainData.joining_date}</td>
                                    </tr>
                                    <tr>
                                        <td>নিজ জেলা</td>
                                        <td></td>
                                    </tr>
                                    <tr>
                                        <td>রক্তের গ্রুপ</td>
                                        <td></td>
                                    </tr>
                                    <tr>
                                        <td>রাজনৈতিক পরিচয়</td>
                                        <td></td>
                                    </tr>
                                </table>
                            </div>
                        </div>
                    </div>
                    }
                    
                    <div className='colam'>
                        <SideBar />
                    </div>
                </div>
                <Footer />
            </div>
            <div className="optionsPart">
                <div className="comment">
                    <img src={CommentImg} alt="" />
                </div>
            </div>
        </>
    )
}

export default Discribe
