import React from 'react'
import TopHeader from '../components/TopHeader'
import Slider from '../components/Slider'
import NavBar from '../components/NavBar'
import Footer from '../components/Footer'
import MainAPI from '../assets/APIs/mainApi'
import { useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import TabTittle from '../components/TabTittle'
import Iframe from 'react-iframe'
import SideBar from '../components/SideBar'
import MainHeader from '../components/MainHeader'
import CommentImg from "../assets/img/comment.gif"
import axios from 'axios'
import { useEffect } from 'react'

function NoticView() {

    // TabTittle(`নোটিশ | ${mainData[0].title}`)

    const pdfURL = `https://zpr.s3cbd.com/assets/zprnotice/`
    const API = `https://zpr.s3cbd.com/data/notice`
    const [mainData, setmainData] = useState([])
    const [isLoading, setIsLoading] = useState(true)
    console.log(mainData);
    const getAPIData = async (api) => {
        try {
            const res = await axios.get(api)
            const Data = await res.data
            setmainData(Data)
            setIsLoading(false)
        } catch (error) {
            console.log(error.message);
        }
    }
    useEffect(() => {
        getAPIData(API)
    }, []);

    return (
        <>
            <div className="contaier">
                <MainHeader />
                <div className="mainContentPart">
                    <div className='colam'>
                        <div className="pageTittle">
                            <h2><Link to="/" >Hme</Link>/<Link to="/notice" >Notice</Link>/Details</h2>
                        </div>
                        {
                            isLoading
                                ?
                                <div>Loading...</div>
                                :
                                <>
                                    <div className="tittle">
                                        <h1>{mainData[0].title}</h1>
                                    </div>
                                    <div className="content">
                                        <Iframe url={pdfURL + mainData[0].file} width='100%' id='iframe' height='900' />
                                    </div>
                                </>
                        }

                    </div>
                    <div className='colam'>
                        <SideBar />
                    </div>
                </div>
                <Footer />
            </div>
            <div className="optionsPart">
                <div className="comment">
                    <img src={CommentImg} alt="" />
                </div>
            </div>
        </>
    )
}

export default NoticView
