import React, { useEffect } from 'react'
import MainAPI from '../assets/APIs/mainApi'
import { Link } from 'react-router-dom'
import { useState } from 'react'
import TabTittle from '../components/TabTittle'
import SideBar from '../components/SideBar'
import MainHeader from '../components/MainHeader'
import CommentImg from "../assets/img/comment.gif"
import axios from 'axios'
import Skeleton from 'react-loading-skeleton'
import Skli_law from '../components/skeletonComp/Skli_Law'
function Law() {
    TabTittle("আইন বিধিমালা")
    const API = `https://zpr.s3cbd.com/data/law`
    const [mainData, setmainData] = useState([])
    const [isLoading, setIsLoading] = useState(true)
    console.log(mainData);
    const getAPIData = async (api) => {
        try {
            const res = await axios.get(api)
            const Data = await res.data
            setmainData(Data)
            setIsLoading(false)
        } catch (error) {
            console.log(error.message);
        }
    }
    useEffect(() => {
        getAPIData(API)
    }, []);
    return (
        <>
            <div className="contaier">
                <MainHeader />
                <div className="mainContentPart">
                    <div className="colam">
                        <div className="pageTittle">
                            <h2><Link to="/" >Hme</Link>/View</h2>
                        </div>
                        <div className="tittle">
                            <h1>আইন বিধিমালা</h1>
                        </div>
                        <table className='table'>
                            <tr className='header'>
                                <th>বিস্তারিত</th>
                                <th>তারিখ</th>
                                <th>ডাউনলোড</th>
                            </tr>
                            {
                                isLoading
                                    ?
                                    <Skli_law/>
                                    :
                                    <>
                                        {
                                            mainData.map((e, i) => {
                                                const { id, low_title, low_date, file } = e
                                                return (
                                                    <>
                                                        <tr>
                                                            <td className='text-center'> <Skeleton/> </td>
                                                            <td className='text-center'><Skeleton/></td>
                                                            <td className='text-center w-40'><Skeleton/></td>
                                                        </tr>
                                                    </>
                                                )
                                            })
                                        }
                                    </>
                            }
                        </table>
                    </div>
                    <div className='colam'>
                        <SideBar />
                    </div>
                </div>
            </div>
            <div className="optionsPart">
                <div className="comment">
                    <img src={CommentImg} alt="" />
                </div>
            </div>
        </>
    )
}

export default Law